// CSS for timepicker widget
// See: http://trentrichardson.com/examples/timepicker/
.ui-timepicker-div {
  .ui-widget-header {
    margin-bottom: 8px;
  }

  dl {
    text-align: left;
  }

  dl dt {
    height: 25px;
    margin-bottom: -25px;
  }

  dl dd {
    margin: 0 10px 10px 65px;
  }

  td {
    font-size: 90%;
  }

  &.ui-tpicker-grid-label {
    background: none;
    border: 0;
    margin: 0;
    padding: 0;
  }

  &.ui-timepicker-rtl {
    direction: rtl;
  }

  &.ui-timepicker-rtl dl {
    text-align: right;
  }

  &.ui-timepicker-rtl dl dd {
    margin: 0 65px 10px 10px;
  }
}



